var usersAdapter=(function() {

    /**
     * Private methods
     */

    function matchExtendedInfo(list, usersWithExtendedInfo) {
        list.map(function(uuser) {
            var u = usersWithExtendedInfo.find(function (e) {
                return uuser.idUser == e.idUser;
            });
            uuser.extendedInfo=u.extendedInfo;
        });
    }

    
    /**
     * Public methods
     */

    /**
     * create a json standard for user information
     * (this is used in modals and usertooltip)
     * if the isAssignee and owner are false the user is participant
     * if the isAssignee and owner are null the user is not in (isAssignee, owner and participant)
     * @param {string} userAssigned guid del usuario
     * @param {string} name 
     * @param {string} username 
     * @param {string} picture 
     * @param {string} email 
     * @param {string} initials 
     * @param {boolean} isAssignee if is true the user shows like assignee
     * @param {boolean} owner if is true the user shows like creator
     * @param {Array} activities 
     * @param {Array} events 
     */
    var createJsonUserInfo = function (userAssigned, name, username, picture, email, initials, isAssignee, owner, activities, events) {
        var participant=owner===false && isAssignee===false;
        if (owner===null && isAssignee===null)
            owner=participant=false;
        if(picture!==undefined)
            picture=picture.replace("data:image/png;base64,data:image/png;base64,", "data:image/png;base64,");
        return {
            userAssigned: userAssigned,
            name: name,
            username: username,
            picture: picture,
            email: email,
            initials: initials,
            isAssignee: isAssignee,
            isParticipant: participant,
            owner: owner,
            activities: activities,
            events: events,
            isAnonymized: userAssigned === bizagi.util.anonymizedUserInformation.id
        };
    };

    /**
     * let to add extendedInfo to each users,assigned,createdBy
     * @param {*} cases 
     * @param {*} usersWithExtendedInfo 
     */
    var matchUsersForExtendedInfoCase=function(cases,usersWithExtendedInfo){
        cases.map(function(casee) {
            if(casee.assignees)
                matchExtendedInfo(casee.assignees,usersWithExtendedInfo);
            if(casee.users)
                matchExtendedInfo(casee.users,usersWithExtendedInfo);
            if(casee.createdBy){
                var u = usersWithExtendedInfo.find(function (e) {
                    return casee.createdBy.idUser == e.idUser;
                });
                casee.createdBy.extendedInfo=u.extendedInfo;
            }
        });
    };

    /**
     * return just the assigned users if exists a only creator user this is ommited
     * @param {Array} users 
     */
    var justAssignees=function(users){
        return users.filter(function(user) {
            return user.isAssignee===true;
        });
    };
    
    return {
        createJsonUserInfo: createJsonUserInfo,
        matchUsersForExtendedInfoCase: matchUsersForExtendedInfoCase,
        justAssignees: justAssignees
    };
}());