/**
 * This file contains all keys that can be change the default functionality
 * of Bizagi, please use it carefully.
 *
 * @author Edward J Morales
 */


// TODO: Please make a documentation for each override

/**
 * Active folders functionality that is used in workportal inbox to create
 * categories of process
 *
 * @type {boolean}
 */
bizagi.override.enableFolder = false;

/**
 * Smart Folders are case repositories that meet certain filters.
 * Each smart folder can be associated with a filter, for example,
 * all files that have been registered as of a determined date.
 *
 * @type {boolean}
 */
bizagi.override.enableSmartFolders = false;

/**
 * This functionality allow a new tap within modal window of new case,
 * that create a shortcut to most used process
 *
 * @type {boolean}
 */
bizagi.override.enableListOfRecentProcesses = false;


/**
 * Unused key. The Author must be define what can do
 *
 * @type {boolean}
 */
bizagi.override.enableCustomizeReports = false;

/**
 * This key enable within admin menu a new item that will
 * open "Multiple case reassigment" module
 *
 * SNAKE-1296
 * @type {boolean}
 */
bizagi.override.enableMultipleCasesReassigment = false;

/**
 * This key enable within admin menu a new item thah will
 * open "Async ECM Upload" module
 *
 * DRAGON-152 Adidas
 * @type {boolean}
 */
bizagi.override.enableAsyncECMUploadJobs = false;

/**
 * This key enable the capability to print an edit form of a grid
 * @type {boolean}
 */
bizagi.override.enablePrintFromEditForm = false;

/**
 * This key can be allow debug window in release environments
 * @type {boolean}
 */
bizagi.enableDebug = false;

/**
 * This key disable old windows and enable new REST development
 * @type {boolean}
 */
//bizagi.override.disableFrankenstein = (typeof BIZAGI_SERVICE_ACTIVATION !== "undefined" && BIZAGI_SERVICE_ACTIVATION) ? true : false;


/**
 * Disable old query forms and enable new tier using REST services
 * @type {boolean}
 */
bizagi.override.disableFrankensteinQueryForms = true;

/**
 * Change default behavior of grid column sort
 * @type String
 * @example desc || asc
 */
//bizagi.override.gridDefaultSortBy= "desc";

/**
 * Expose interface to access to DOM elements
 * @type {boolean}
 */
bizagi.override.enableE2EInterface = true;

/**
 * Disable old inbox
 * @type {boolean}
 */
bizagi.override.disableOldInbox = true;

/**
 * Time out to remove elements that not will be used anymore
 * @type {number} Milliseconds
 */
bizagi.override.disposeTime = 50;

/**
 * Enable REST batch request functionality
 * @type {boolean}
 */
bizagi.override.enableBatchRequest = true;

/**
 * force all request that consume a REST through Web API will be sending in a batch request
 * @type {boolean}
 */
bizagi.override.forceBatchRequest = false;

/**
 * Security option to enable visualization of current assignee
 * @type {boolean}
 */
bizagi.override.showAssignees = true;

/**
 *
 * @type {boolean}
 */
bizagi.override.enableMySearch = true;

/**
 *
 * @type {boolean}
 */
bizagi.override.enableSecurityCaseDashBoard = false;

/**
 * Show classic view(Sub levels of application and entities)
 * @type {boolean}
 */
bizagi.override.enableQueriesClassicMenu = false;

/**
 * disable cancel cases functionality in manage cases and hide the Cancel cases tab in Admin cases
 * @type {boolean}
 */
bizagi.override.disableAbortCases = false;



/**
 * Enable "plus icon" on inbox, and load Adhoc Process module
 * @type {boolean}
 */
bizagi.override.enableAdhocProcess = false;

/**
 * Enable "BCC/CC" feature for Notification Task on Live Processes
 * @type {boolean}
 */
bizagi.override.enableCCBCCFeatureOnLiveProcesses = true;

/**
 * Enable "Review Mode" feature on Live Processes
 * @type {boolean}
 */
bizagi.override.enableReviewModeFeatureOnLiveProcesses = true;

/**
 * Enable "Filter by process grouo" feature on Live Processes
 * @type {boolean}
 */
bizagi.override.enableFilterByProcessGroupOnLiveProcesses = false;

/**
 * Enable "Support localization in the process modeler" feature on Live Processes
 * @type {boolean}
 */
bizagi.override.enableLocalizationInModelerOnLiveProcesses = true;

/**
 * Enable "Support predefined performers in the process modeler" feature on Live Processes
 * @type {boolean}
 */
bizagi.override.enablePredefinedPerformersOnLiveProcesses = true;


/**
 * Enable "Show timeline icon in inbox" feature on Workportal
 * @type {boolean}
 */
bizagi.override.enableFeatureShowTimelineButton = true;

/**
 * Enable "Display Options" feature on Workportal
 * @type {boolean}
 */
bizagi.override.enableFeatureDisplayOptions = false;

/**
 * Enable "Mazimize Modals" option inside  "Display Options" feature on Workportal
 * @type {boolean}
 */
bizagi.override.enableMaximizeModalsOption = false;

/**
 * Enable "Start Page" option inside  "Display Options" feature on Workportal
 * @type {boolean}
 */
bizagi.override.enableStartPageDisplayOption = false;

/**
 * Enable "Ping to Server" feature on Workportal
 * @type {boolean}
 */
bizagi.override.enablePing = false;

/**
* Use multiaction implementation for get ECM metadata
* @type {boolean}
*/
bizagi.override.enableECMMetadataMultiaction = true;

 /* Validate the form before than buttons' actions
 */
bizagi.override.validateGridsOnButtonClick = false;

/* Enable custom formats form
 */
bizagi.override.enableCustomFormats = false;

/* Enable share activity
 */
bizagi.override.enableShareActivity = true;

/* 
 * Enable format in Money type column in the queries results
 */
bizagi.override.enableFormatMoneyColumnQueryResult = true;

/*
 * Refresh grid parent of cell when there are expressions on visible property
 */
bizagi.override.enableGridRefreshAfterCellRefresh = false;

/**
 * Show the QR mobile in user menu
 */
bizagi.override.showQrMobileMenu = true;

/**
 * Allow execution action before to refresh (DFAS Feature)
 */
bizagi.override.enableExecuteRefreshAction = true;
