/**
 * Name: Bizagi Workportal Users Modal
 */

bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.users.modal", {}, {
    /**
     * Constructor
     */
    init: function (workportalFacade, dataService, params) {
        var self = this;
        self.usuarios = params.data;
        self.showActivityInfo = params.data.users !== undefined;
        if (self.showActivityInfo) {
            self.usuarios = params.data.users;
            self.isActivity = params.data.isActivity;
            self.activityName = params.data.activityName;
        }
        
        // Call base
        self._super(workportalFacade, dataService, params);

        //Load template
        self.loadTemplates({
            "users-modal": bizagi.getTemplate("bizagi.workportal.desktop.widgets.users.modal").concat("#users-modal")
        });

    },
 
    renderContent: function(){
        var self = this;
        var template = self.getTemplate('users-modal');
        self.content = template.render({
            usuarios: self.usuarios,
            isActivity: self.isActivity,
            activityName: self.activityName,
            showActivityInfo: self.showActivityInfo
        });
        self.clickArrow(self.content);
        return self.content;
    },
 
    postRender: function () {
        var self = this;
       
    },

    clean: function () {
        var self = this;
    },
    clickArrow: function(context){
        $(".expand__arrow", context).click(function(event){
            var contentItemSelected = $(this).parent().parent();
            if ($(".users_collapse", contentItemSelected).css('display') == 'none'){
                $(this).addClass("expand__arrow--active");
                $(".users_collapse", contentItemSelected).show();
            } else {
                $(this).removeClass("expand__arrow--active");
                $(".users_collapse", contentItemSelected).hide();
            }
    
        });
    },
  
 });
 
 bizagi.injector.register('bizagi.workportal.widgets.users.modal', ['workportalFacade', 'dataService', bizagi.workportal.widgets.users.modal]);
 